	module.exports = " <div class=container _v-d6f1f366=\"\"> <div class=jumbotron _v-d6f1f366=\"\"> <h2 _v-d6f1f366=\"\">项目说明</h2> <p _v-d6f1f366=\"\"> </p><h3 _v-d6f1f366=\"\">文件说明</h3> <p _v-d6f1f366=\"\"> AppHeader.vue 头部组件<br _v-d6f1f366=\"\"> AddTopic.vue 主页添加话题区<br _v-d6f1f366=\"\"> Home.vue 主页主内容区<br _v-d6f1f366=\"\"> Content.vue 文章详情页面<br _v-d6f1f366=\"\"> Modify.vue 修改文章页面<br _v-d6f1f366=\"\"> Author.vue 单用户话题列表页面<br _v-d6f1f366=\"\"> common.js 复用函数文件<br _v-d6f1f366=\"\"> </p> <h3 _v-d6f1f366=\"\">路由及页面结构和props数据</h3> <p _v-d6f1f366=\"\"> 首页： '/home'：AppHeader + AddTopic <br _v-d6f1f366=\"\"> 文章详情页： '/home/content/:id'：AppHeader + Content ['usr', 'showConfirm'] <br _v-d6f1f366=\"\"> 文章修改页\t： '/home/modify/:id'：AppHeader + Modify ['tips'] <br _v-d6f1f366=\"\"> 用户话题页\t： '/home/author/:user'：AppHeader + Author ['usr', 'showConfirm'] <br _v-d6f1f366=\"\"> </p> <h3 _v-d6f1f366=\"\">功能及实现说明</h3> <h4 _v-d6f1f366=\"\">首页</h4> <h5 _v-d6f1f366=\"\">注册：</h5> <p _v-d6f1f366=\"\">提交前校验两次输入密码是否一致，成功注册和用户名已被注册三者均有反馈信息</p> <h5 _v-d6f1f366=\"\">登陆：</h5> <p _v-d6f1f366=\"\">登陆状态在localStorage中保存两个键值对：\"login\"为boolean值，对应登陆和退出状态；\"user\"为string值，为当前登陆用户，无登陆则为''。</p> <h5 _v-d6f1f366=\"\">话题列表：</h5> <p _v-d6f1f366=\"\">默认页码为\"1\"，点击相应页码ajax请求对应顺序的文章，点击“下一页”获取更多文章，当返回文章数目少于默认的10条时，“下一页”按钮无法点击。</p> <h5 _v-d6f1f366=\"\">动画：</h5> <p _v-d6f1f366=\"\">\"添加话题\"组件的显示和消失，从/home跳转到其他页面时有动画效果</p> <h4 _v-d6f1f366=\"\">效果实现</h4> <h5 _v-d6f1f366=\"\">操作信息反馈：</h5> <p _v-d6f1f366=\"\">\"添加话题\"、\"修改话题\"使用同一个模态框展示反馈内容，调用时通过\"tips\"变量可定制反馈文字和模态框的样式；\"删除话题\"多一步确认模态框，删除成功提示也使用前者的模态框</p> <h5 _v-d6f1f366=\"\">修改/删除后路由切换：</h5> <p _v-d6f1f366=\"\">入口：主页(/home)/单用户话题列表页面(/home/author)/文章详情页(/home/content)</p> <p _v-d6f1f366=\"\">返回路由：<br _v-d6f1f366=\"\">修改操作：主页(更新/home/modify~/home)/用户话题列表(更新/home/modify~/home/author)/文章详情页(更新/home/modify~/home/content) <br _v-d6f1f366=\"\">删除操作：主页(更新)/用户话题列表(更新)/主页(更新/home/content~/home)</p> <p _v-d6f1f366=\"\"></p> </div> </div> ";

